*, *::before, *::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 0;
  background: url("/img/blue.jpg") no-repeat center center fixed; /*ADD BROWSER COMPATIBLITIES*/
  background-size: cover;
  color: white;
}

main {
  // width: 100%;
  display: flex;
  flex-direction: column;

}

header {
  display: flex;
  position: sticky;
  top: 0;
  padding: 0 1rem 0 1rem;
  justify-content: flex-end;
  background-color: #021672;
  z-index: 100;
  /* align-items: flex-end; */ /* this will move bar menu to the bottom*/
}

.logo {
  flex-grow: 3;
}

.nav-container {
  flex-grow: 1;
}

.nav-toggle {
  display: none;
}

.nav-toggle-label {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 1em;
  height: 100%;
  display: flex;
  align-items: center;
}

.nav-toggle-label span,
.nav-toggle-label span::before,
.nav-toggle-label span::after {
  display: block;
  background: white;
  height: 2px;
  width: 2em;
  border-radius: 2px;
  position: relative;
}

.nav-toggle-label span::before,
.nav-toggle-label span::after {
  content: '';
  position: absolute;
}
  
.nav-toggle-label span::before {
  bottom: 7px;
}

.nav-toggle-label span::after {
  top: 7px;
}

nav {
  position: absolute;
  text-align: right;
  top: 100%;
  right: 0;
  width: 100%;
  display: none;
}


nav ul li a {
  margin: 0;
  padding: 0;
  margin-bottom: 1em;
  margin-right: 1em;
  text-decoration: none;
  color: white;
}

nav ul {
  list-style-type: none;
}

h1:hover {
  color: #0478DF;
}


a:hover {
  color: #0478DF;
  text-shadow: 1px 0.5px white;
  text-decoration: none;
}

.nav-toggle:checked ~ nav {
  display: block;
}

.nav-stack {
  display: flex;
  flex-direction: column;
}

h1 {
  letter-spacing: 0.5em;
  text-shadow: 3px 2px silver;
}

h2 {
  letter-spacing: 0.5em;
  text-shadow: 1px 2px gray;
}

h3 {
  letter-spacing: 0.25em;
}


section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 1rem 0 1rem 0;
  padding: 2rem 0 2rem 0;
  text-align: center;
  background: hsla(240, 100%, 25%, 0.3);
}

.parent-container-skills {
  flex-wrap: wrap;
}

.section-content-container {
  width: 50rem;
}

.container-skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  flex-direction: column;
  
}

.container-skills1 {
  display: flex;
  justify-content: center;
  align-content: space-between; 
}

.container-skills2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: space-between;
  padding: 5rem 0 0 0;
}

.skills-icons {
  width:50px;
  height: 50px;
  margin: 2rem;
}

.react-icon {
  width: 100px;
  height: 100px;
  margin: 2rem;
}

.mysql-icon {
  width: 100px;
  height: 100px;
  margin: 2rem;
}

.card-body {
  width: 150px;
  height: auto;
  padding: 0;
  background-image: url("/portfolio/src/img/Gnulove.medium.jpg");
}

span {
  color: green;
  font-weight: bold;
}

.p-blue {
  color: #08A2F7;
  font-weight: bold;
}

// .details {
//   transform: scale(0);
//   transition: .5s;
// }


.footer-icons {
  width: 25px;
  height: auto;
}

footer {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  position: sticky;
  bottom: 0;
  padding: 0 1rem 0 1rem;
}

.contact {
  width: 0;
  position: relative;
  animation: mymove 5s;
  }

@keyframes mymove {
  0%   {bottom: 75px;}
  25% {bottom: 200px;}
  75%  {bottom: 50px}
  100% {bottom: 0px;}
}

.contact-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  
}

.contact-form-message {
  display: flex;
  flex-direction: column;
  align-content: space-between;
}

.txt {
  padding: 2em 2em 1em 2em;
}

input {
  width: 15.5rem;
  height: 3rem;
  border-radius: 0.5em;
}

textarea {
  width: 35rem;
  height: 10rem;
  background-color: white;
  border-radius: 0.5em;
}

button {
  height: 4rem;
  margin: 1em 0 0 0;
  background:#0478DF;
  border-radius: 0.5em;
  border: none;
  color: white;
  border: 1px solid #021672;
}

@media (max-width: 658px) {
  .contact-form-message {
    textarea {
      width: 30rem;
      height: 8rem;
    }
    input {
      width: 8rem;
      height: 2rem;
    } 
  }
  .txt{
    padding:1rem;
  }
}

